.seccion-doble {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  background-color: rgba(255, 252, 250, 0.1588);
  padding-left: 5%;
  padding-right: 5%;
  margin-bottom: 40px;
  justify-content: center;
  align-items: center;
}

.imagen-container img {
  max-width: 750px;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
}

.texto-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 60px;
  margin: 0 auto;
  text-align: left;
}

.texto-container h2 {
  font-size: 3rem;
  margin-bottom: 10px;
  text-align: center;
}

.texto-container p {
  font-size: 1.1rem;
line-height: 1.5;
}




